 
.inform-item::before{
    content : "";
    position: absolute;
    width: 16px;
    height: 16px;
    border-radius: 50px;
    left: -9px;
    top: 0;
    border: 3px solid #FFD6BA;
    background-color: #fff;

} 


.inform-title{
  color: #8dd4c5;
}


.bg-pop-up{
  margin: 0;
  background-color: rgba(0, 0,0, .7);
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  height:100%;
  width: 100%;
  overflow: hidden;
  /* color: rgb(20, 30, 35); */
  position: fixed;
  top: 0px;
  left: 0px;
  z-index:90;  
}



.close{
  color: white;
  position: absolute;
  font-size: 25px;
  cursor: pointer;
}

.popupRightArea{
  background-color: #555B6E;
}

.popupRight{
  /* border: 1px solid red; */
  justify-content: center;
  align-items: center;
}

.pop-up-content .cv-area{
  width: 100%;
  /* height: 550px; */
  height: 450px;
  margin-top: 20px;
  overflow-y: scroll;
  border: none;
 
}

/* width */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #FFD6BA;
  
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #FFD6BA;
  border-radius: 8px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #FFD6BA;
}
  









