/* en son 601 px e kadar*/
@media screen and ( max-width: 601px){

  .popupRightArea img{
    width: 45%;
  }

  .popupRightArea h4, h5{
    font-size: 1.1rem;
  }

  .bg-pop-up .pop-up-content .container{
    height: 50%;
    top: 0px;
    max-height: 100%;
    width: 90%;
    display: grid;
    /* grid-template-rows: auto 3fr auto; */
    justify-items: center;
    position: relative; 
    overflow: hidden;
    max-height: 750px;

  }
  .bg-pop-up .pop-up-content .cv-area{
    
     max-height: 350px;
     padding: 0px 0px;
     margin: 0px;
  }

  .cv-area p{
    font-size: 0.9rem;
  }
  .cv-area strong{
    font-size: 1rem;
  }

  
 

}



